import Vue from 'vue'
import Router from 'vue-router'
import Vuex from 'vuex'
const VantHomePage = r => require.ensure([], () => r(require('@/components/index/VantHomePage')), 'VantHomePage')
const Detail = r => require.ensure([], () => r(require('@/components//detail/Detail')), 'Detail')
const Activity = r => require.ensure([], () => r(require('@/components/activity/Activity')), 'Activity')
const Search = r => require.ensure([], () => r(require('@/components/search/Search')), 'Search')
const Fitter = r => require.ensure([], () => r(require('@/components/fitter/Fitter')), 'Fitter')
const UTV = r => require.ensure([], () => r(require('@/components/UTV/UTV')), 'UTV')


// const VantHomePage = () => import( /* webpackChunkName: "other" */'../components/index/VantHomePage.vue')
// const Detail = () => import( /* webpackChunkName: "other" */'../components/detail/Detail.vue')
// const Activity = () => import( /* webpackChunkName: "other" */'../components/activity/Activity.vue')
// const Search = () => import( /* webpackChunkName: "other" */'../components/search/Search.vue')
// const Fitter = () => import( /* webpackChunkName: "other" */'../components/fitter/Fitter.vue')
// const UTV = () => import( /* webpackChunkName: "other" */'../components/UTV/UTV.vue')

// import VantHomePage from '../components/index/VantHomePage.vue'
// import Detail from '../components/detail/Detail.vue'
// import Activity from '../components/activity/Activity.vue'
// import Search from "../components/search/Search.vue"
// import Fitter from "../components/fitter/Fitter.vue"
// import UTV from "../components/UTV/UTV.vue"

Vue.use(Router)
Vue.use(Vuex)

export default new Router({
  base: '/freeStyleTourism/',
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: '/index/recommend'
    },
    {
      path: '/index/:id',
      component: VantHomePage, //VantHomePage
      // 首页
      props: true,
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/search',
      name: 'search',
      component: Search,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/fitter',
      name: 'fitter',
      component: Fitter,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/detail',
      name: 'detail',
      component: Detail,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/activity',
      name: 'activity',
      component: Activity,
      props: true,
      meta: {
        keepAlive: false
      }
    },
    {
      path: '/UTV',
      name: 'UTV',
      component: UTV,
      props: true,
      meta: {
        keepAlive: false
      }
    }
  ]
})



